<template>
  <div class="batch-main">
    <iframe id="myiframe" src="../../../static/batchRun/batchRun.html" scrolling="no" frameborder="0" @load="changeFrameHeight"></iframe>
  </div>
</template>

<script>
export default {
  data() {
    return {}
  },
  mounted() {
    window.onresize = function() {
      this.changeFrameHeight()
    }
  },
  methods: {
    changeFrameHeight() {
      const ifm = document.getElementById('myiframe')
      const sWidth = document.getElementsByClassName('batch-main')[0].offsetWidth
      ifm.height = document.documentElement.clientHeight
      ifm.width = sWidth
    }
  }
}
</script>



